import React, { Component } from 'react';
import Data from './../../data/home';
import HomeWidget from './../../components/HomeWidget';
import './home.css';

class Home extends Component {
  	render() {
    	return (
			<main className="home">
				{Data.map((widget, i) => {
					return (
						<HomeWidget 
							key={i}
							title={widget.title}
							copy={widget.copy}
							widgetClass={widget.class}
							logo={widget.logo}
						/>
					);
				})}
			</main>
    	);
  	}
}

export default Home;



