import React from 'react';
import { NavLink } from 'react-router-dom';
import './back.css';

const Back = (props) => {
	return(
		<NavLink 
			className={`back ${props.currentpage}`}
			exact={true}
			to={`${process.env.PUBLIC_URL}/home`}
		>
			<i className="icon-font icon-home" />
		</NavLink>
	)
}

export default Back;


