import React, { Component } from 'react';
import axios from 'axios';
import Data from './../data/modal';
import './modal.css';
// add axios for from submit

class Modal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			data : {
				name : "",
				email : "",
				message : ""
			},
			sent : false
		}
	}
	handleChange(changed, newValue) {
		this.setState({ data: { ...this.state.data, [changed]: newValue } });
	}
	sendMail(e) {
		e.preventDefault();
		axios({
			url: "https://formspree.io/thomasjjbush@gmail.com",
			method: "POST",
			data: this.state.data,
			dataType: "json",
			success: console.log("SENT"),
			failure: this.setState({
				sent : false
			})
		});
	}
	render() {
		return(
			<div className="modal">
				<div className="contact-details">
					{Data.contactInfo.map((info, i) => {
						return (
							<a
								key={i}
								href={info.url}
								target={`_${info.target}`} 
								className={`Amatic detail ${info.contact}`}
							>
								{[...info.text]}
							</a>
						)
					})}
				</div>
				<form className="contact-form" name="contact-form">
					<p>{Data.form.text}</p>
					{Data.form.inputs.map((form, i) => {
						return (
							<div key={i}>
								{form.type === "textarea" 
									?  	<textarea
											type={form.type}
											name={form.name}
											placeholder={form.placeholder}
											rows="10"
											className="textarea"
											required={true}
											value={this.state.data[form.name]}
											onChange={(e) => this.handleChange(e.target.name, e.target.value)}
										/>
									:  	<input	
											type={form.type}
											name={form.name}
											placeholder={form.placeholder}
											className="input"
											required={true}
											value={this.state.data[form.name]}
											onChange={(e) => this.handleChange(e.target.name, e.target.value)}
										/>
								}
							</div>
						);	
					})}
					<input	
						type="submit"
						name="submit"
						value="Send your message "
						className="Amatic input submit"
					/>
				</form>
			</div>
		);
	}
}

export default Modal;


