import React, { Component } from 'react';
import Modal from './../components/Modal';
import Data from './../data/footer';
import './footer.css';

class Footer extends Component {
	constructor(props) {
		super(props);
		this.state = {
			active : false
		}
		this.handleClick = this.handleClick.bind(this);
	}
	handleClick() {
		this.setState(prevState => ({
			active: !prevState.active
		}));
	}
	render() {
		return(
			<footer className={`footer ${this.state.active ? `open` : `closed`}`}>
				<div className="cta-area">
					<button
						className="cta Amatic"
						onClick={this.handleClick}
					>
						{ this.state.active ? "Close" : "Book Now" } 
					</button>
					<div className="social">
						{Data.map((icon, i) => {
							return (
								<a
									key={i}
									href={icon.url}
									target="_blank"
								>
									<i className={`icon-font icon-${icon.social}`} />
								</a>
							);
						})}
					</div>
				</div>
				<Modal />
			</footer>
		);
	}
}

export default Footer;


