import React from 'react';
import HomeWidget from './HomeWidget';
import './return.css';

const Return = (props) => {
	return(
		<nav className="return">
			<div className="dummy" />
			{props.Data.map((widget, i) => {
				return (
					<HomeWidget 
						key={i}
						title={widget.title}
						copy={widget.copy}
						widgetClass={widget.class}
						logo={widget.logo}
					/>
				);
			})}
		</nav>
	)
}

export default Return;


