import React from 'react';
import { NavLink } from 'react-router-dom';
import './homeWidget.css';
import Logo from '../assets/img/home/farah.jpg';

const HomeWidget = (props) => {
	return(
		<NavLink 
			className={`homeWidget ${props.widgetClass.join(' ')}`}
			exact={true}
			to={`${process.env.PUBLIC_URL}/${props.widgetClass[0]}`}
		>
			{
				props.logo
				? <img src={Logo} alt="Farah" className="logo" />
				: <h1 className="Amatic">{props.widgetClass[0]} </h1>
			}
		</NavLink>
	)
}

export default HomeWidget;



