import React, { Component } from 'react';
import NotFound404 from './../../components/NotFound404';
import Back from './../../components/Back';

class NotFound extends Component {
  	render() {
    	return (
			<div className="notfound">
				<NotFound404 />
				<Back />
			</div>
    	);
  	}
}

export default NotFound;



