import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Home from './containers/home/Home';
import Hair from './containers/hair/Hair';
import Beauty from './containers/beauty/Beauty';
import Specialist from './containers/specialist/Specialist';
import NotFound from './containers/notfound/NotFound';
import Footer from './components/Footer';

class App extends Component {
  	render() {
    	return (
			<div className="app">
				<Switch>
					<Redirect exact from={`/${process.env.PUBLIC_URL}`} to={`${process.env.PUBLIC_URL}/home`}  />
					<Route 
						path={`${process.env.PUBLIC_URL}/home`} 
						component={Home}
					/>
					<Route 
						path={`${process.env.PUBLIC_URL}/hair`} 
						component={Hair}
					/>
					<Route 
						path={`${process.env.PUBLIC_URL}/beauty`}
						component={Beauty}
					/>
					<Route 
						path={`${process.env.PUBLIC_URL}/specialist`}
						component={Specialist}
					/>
					<Route 
						path={`${process.env.PUBLIC_URL}/*`}
						component={NotFound}
					/>
				</Switch>
				<Footer />
			</div>
    	);
  	}
}

export default App;



