import React, { Component } from 'react';
import Select from 'react-select';
import 'react-select/dist/react-select.css';
import './treatmentsTable.css';

class TreatmentsTable extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedOption : this.props.Data.options[0]
        }
	}
	handleChange = (selectedOption) => {
		this.setState({ selectedOption });
	}
	render() {
		const { selectedOption } = this.state;
		return (
			<div className="table">
				<Select
					name="form-field-name"
					value={selectedOption}
					onChange={this.handleChange}
					options={this.props.Data.options}
					className={`selectStyling ${this.props.currentpage}`}
					searchable={false}
				/>
				<div>
					{selectedOption === null 
						? null
						: this.props.Data[selectedOption.value].map((treatment, i) => {
							return (
								<div key={i} className={`treatment ${this.props.currentpage}`}>
									<div className="content">
										<p className="Amatic">{treatment.name}</p>
										{ treatment.description === null 
										? null 
										: <p>{treatment.description}</p>
										}
									</div>
									<p 
										className={`Amatic pricing ${this.props.currentpage} ${treatment.discount ? `discount` : `noDiscount`}`}
										data-discount={treatment.discountPrice}
									>
										{treatment.price}.00
									</p>
								</div>
							);
						})
					}
				</div>
				<div className="disclaimer">
					{this.props.Data.disclaimers[selectedOption.value].map((disc, i) => {
							return (
								<p key={i}>{i+1}: {disc}</p>
							);
						})
					}
				</div>
			</div>
		);
	}
}

export default TreatmentsTable;


