import React from 'react';
import Logo from './../assets/img/home/farah.jpg';
import './hero.css';

const Hero = (props) => {
	return(
		<div className={`hero ${props.currentpage}`}>
			<h1 className={`Amatic ${props.currentpage}`}>
				{props.currentpage}
			</h1>
			<img src={Logo} className="logo" alt="Farah" />
		</div>
	)
}

export default Hero;


