import React from 'react';
import './notFound404.css';

const Return = (props) => {
	const error = [
		4,0,4
	]
	return(
		<div className="notFound404">
			<h1 className="Amatic">
				{error.map((error, i) => {
					return <span key={i}>{error}</span>
				})}
			</h1>
		</div>
	)
}

export default Return;


